
/* Start::font sizes */
// (1px-30px are linear, above 30px are step increment of 1)
.fs-1 {
  @apply text-[2.5rem];
}
.fs-2 {
  @apply text-[2rem];
}
.fs-3 {
  @apply text-[1.75rem];
}
.fs-4 {
  @apply text-[1.5rem];
}
.fs-5 {
  @apply text-[1.25rem];
}
.fs-6 {
  @apply text-[1rem];
}

/* End::font sizes */
.blockquote-container {
  @apply p-[1.25rem] border border-solid border-defaultborder border-t-[4px] rounded-md relative
  before:absolute before:content-["\f6b0"] before:w-[1.5rem] before:h-[1.5rem] before:text-[0.813rem] before:font-semibold before:border before:border-solid
  before:border-defaultborder before:bg-white before:-top-[0.875rem] before:items-center before:flex before:p-0 before:justify-center before:rounded-[3.125rem] before:start-[50%];
}

.blockquote.custom-blockquote {
  @apply text-[0.85rem] font-semibold p-4 rounded-md relative before:content-["\f6b0"] before:z-[0] before:text-[4rem] before:absolute before:-top-[1.5rem] before:-end-4;
  .quote-icon{
    @apply w-[2.5rem] h-[2.5rem] flex items-center justify-center rounded-[3.125rem] absolute -top-4 -end-4 bg-white;
    i {
      @apply text-[1.75rem] font-semibold;
    }
  }
  &.primary {
    @apply bg-primary/10 border-s-2 border-solid border-primary border-t-2 before:text-primary/10;
    .quote-icon i {
      @apply text-primary ;
    } 
  }
  &.secondary {
     @apply bg-secondary/10 border-s-2 border-solid border-secondary border-t-2 before:text-secondary/10;
    .quote-icon i {
      @apply text-secondary ;
    }
  }
  &.info {
    @apply bg-info/10 border-s-2 border-solid border-info border-t-2 before:text-info/10;
    .quote-icon i {
      @apply text-info ;
    }
  }
  &.warning {
    @apply bg-warning/10 border-s-2 border-solid border-warning border-t-2 before:text-warning/10;
    .quote-icon i {
      @apply text-warning ;
    }
  }
  &.success {
    @apply bg-success/10 border-s-2 border-solid border-success border-t-2 before:text-success/10;
    .quote-icon i {
      @apply text-success;
    }
  }
  &.danger {
    @apply bg-danger/10 border-s-2 border-solid border-danger border-t-2 before:text-danger/10;
    .quote-icon i {
      @apply text-danger ;
    }
  }
}
dt {
  @apply font-[700];
}
dd {
  @apply mb-2 ml-0;
}
.text-lowercase {
  @apply lowercase #{!important};
}

.text-uppercase {
  @apply uppercase #{!important};
}

.text-capitalize {
  @apply capitalize #{!important};
}
/* End::Blockquote */
